import { type NextRequest, NextResponse } from "next/server";
import crypto from "crypto";
import { db } from "~/server/db";
import { auth } from "~/server/auth";

/**
 * Custom Last.fm callback handler
 * Handles Last.fm's implicit OAuth flow (token in URL query param)
 * Bypasses Auth.js OAuth validation which doesn't support implicit flows
 *
 * This route must be more specific than /api/auth/[...nextauth] so it takes precedence
 */

interface LastfmProfile {
	user: {
		id?: string;
		name: string;
		realname?: string;
		url?: string;
		image?: Array<{ size: string; "#text": string }>;
		registered?: { unixtime: string };
		playcount?: string;
	};
}

function generateApiSig(
	params: Record<string, string>,
	apiSecret: string
): string {
	const sorted = Object.keys(params)
		.sort()
		.map((k) => `${k}${params[k]}`)
		.join("");

	return crypto
		.createHash("md5")
		.update(sorted + apiSecret)
		.digest("hex");
}

async function getSessionKey(
	token: string,
	apiKey: string,
	apiSecret: string
): Promise<string> {
	const params: Record<string, string> = {
		api_key: apiKey,
		method: "auth.getSession",
		token: token,
	};

	const api_sig = generateApiSig(params, apiSecret);

	const queryParams = new URLSearchParams({
		...params,
		api_sig,
		format: "json",
	});

	const response = await fetch(
		`https://ws.audioscrobbler.com/2.0/?${queryParams}`,
		{
			method: "GET",
		}
	);

	if (!response.ok) {
		throw new Error(
			`Failed to get Last.fm session key: ${response.statusText}`
		);
	}

	const data = (await response.json()) as {
		session?: { key: string; user: string };
		error?: number;
		message?: string;
	};

	if (data.error) {
		throw new Error(
			`Last.fm API error (${data.error}): ${data.message}`
		);
	}

	if (!data.session?.key) {
		throw new Error("No session key returned from Last.fm");
	}

	return data.session.key;
}

async function getUserProfile(
	apiKey: string,
	sessionKey: string
): Promise<LastfmProfile> {
	const params: Record<string, string> = {
		api_key: apiKey,
		method: "user.getInfo",
		sk: sessionKey,
	};

	const api_sig = generateApiSig(params, process.env.LASTFM_API_SECRET || "");

	const queryParams = new URLSearchParams({
		...params,
		api_sig,
		format: "json",
	});

	const response = await fetch(
		`https://ws.audioscrobbler.com/2.0/?${queryParams}`,
		{
			method: "GET",
		}
	);

	if (!response.ok) {
		throw new Error(
			`Failed to get Last.fm user profile: ${response.statusText}`
		);
	}

	const data = (await response.json()) as {
		user?: LastfmProfile["user"];
		error?: number;
		message?: string;
	};

	if (data.error) {
		throw new Error(
			`Last.fm API error (${data.error}): ${data.message}`
		);
	}

	if (!data.user) {
		throw new Error("No user data returned from Last.fm");
	}

	return { user: data.user };
}

export async function GET(request: NextRequest) {
	try {
		// Get token from query params
		const token = request.nextUrl.searchParams.get("token");

		if (!token) {
			console.error("[Last.fm] No token in callback URL");
			const url = new URL("/", request.nextUrl.origin);
			url.searchParams.set("error", "NoToken");
			return NextResponse.redirect(url);
		}

		console.log("[Last.fm] Received callback with token:", token);

		const apiKey = process.env.LASTFM_API_KEY;
		const apiSecret = process.env.LASTFM_API_SECRET;

		if (!apiKey || !apiSecret) {
			console.error("[Last.fm] API credentials not configured");
			const url = new URL("/", request.nextUrl.origin);
			url.searchParams.set("error", "MissingCredentials");
			return NextResponse.redirect(url);
		}

		// Exchange token for session key
		console.log("[Last.fm] Exchanging token for session key...");
		const sessionKey = await getSessionKey(token, apiKey, apiSecret);
		console.log("[Last.fm] Successfully got session key");

		// Get user profile
		console.log("[Last.fm] Fetching user profile...");
		const profile = await getUserProfile(apiKey, sessionKey);
		console.log("[Last.fm] Got user profile:", {
			name: profile.user.name,
			realname: profile.user.realname,
		});

		// Last.fm uses username as unique identifier, not id
		const providerAccountId = profile.user.name;

		if (!providerAccountId) {
			throw new Error("Last.fm user profile missing name field");
		}

		// Create or update user in database
		const user = await db.user.upsert({
			where: { email: `${profile.user.name}@last.fm` },
			update: {
				name: profile.user.name || profile.user.realname,
				image:
					profile.user.image?.find((img) => img.size === "large")?.[
						"#text"
					] || null,
			},
			create: {
				email: `${profile.user.name}@last.fm`,
				name: profile.user.name || profile.user.realname,
				image:
					profile.user.image?.find((img) => img.size === "large")?.[
						"#text"
					] || null,
			},
		});

		console.log("[Last.fm] Created/updated user:", user.id);

		// Create or update account (stores Last.fm session key)
		// Use username as providerAccountId since Last.fm doesn't return id field
		const account = await db.account.upsert({
			where: {
				provider_providerAccountId: {
					provider: "lastfm",
					providerAccountId: providerAccountId,
				},
			},
			update: {
				access_token: sessionKey,
				token_type: "Bearer",
			},
			create: {
				userId: user.id,
				provider: "lastfm",
				providerAccountId: providerAccountId,
				type: "oauth",
				access_token: sessionKey,
				token_type: "Bearer",
			},
		});

		console.log("[Last.fm] Created/updated account with session key");

		// Create NextAuth session in database
		const sessionToken = crypto.randomUUID();
		const expires = new Date(Date.now() + 30 * 24 * 60 * 60 * 1000); // 30 days

		await db.session.create({
			data: {
				sessionToken,
				userId: user.id,
				expires,
			},
		});

		console.log("[Last.fm] Created session token");

		// Redirect to dashboard with session cookie
		const response = NextResponse.redirect(new URL("/dashboard", request.nextUrl.origin));

		// Set NextAuth session cookie
		response.cookies.set("next-auth.session-token", sessionToken, {
			httpOnly: true,
			maxAge: 30 * 24 * 60 * 60, // 30 days
			path: "/",
			secure: process.env.NODE_ENV === "production",
			sameSite: "lax",
		});

		console.log("[Last.fm] User authenticated successfully, redirecting to dashboard");

		return response;
	} catch (error) {
		console.error("[Last.fm] Callback error:", error);
		const errorMessage = error instanceof Error ? error.message : "Unknown error";

		const url = new URL("/", request.nextUrl.origin);
		url.searchParams.set("error", encodeURIComponent(errorMessage));
		return NextResponse.redirect(url);
	}
}
